import React from 'react';
import { Link } from "react-router-dom";
import logo from "./images/icon.png"
import Login from './components/Login'

//var loggedIn = true;
const loggedIn = React.createContext(false);

const NavBar = () => (

    <>

<nav className="navbar navbar-expand-lg navbar-light bg-light">
<Link to="/">
  <img src={logo}width="150" height="100" alt=""/>
</Link>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarNav">

    <ul className="navbar-nav mr-auto">

      <li className="nav-item">
        <Link to="/" className="nav-link">MainPage</Link>
      </li>

      <li className="nav-item">
        <Link to="/AboutPage" className="nav-link">AboutPage</Link>
      </li>

      <li className="nav-item">
        <Link to="/DisplayGraphPage" className="nav-link">Display Graph</Link>
      </li>
                    
      <li className="nav-item">
        <Link to="/ViewSavedGraphsPage" className="nav-link">View Saved Graphs</Link>
                    </li>

      <li className="nav-item">
        <Link to="/FAQPage" className="nav-link">FAQ</Link>
            </li>

    </ul>
    <div>
    <Login />
    </div>   
  </div>
</nav>
</>

);

export default NavBar;

