import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

import MainPage from './pages/MainPage';
import './App.css';
import AboutPage from './pages/AboutPage';
import NavBar from './NavBar';
import NotFound404 from './pages/NotFound404';
import graph from './pages/DisplayGraphPage';
import savedGraphs from './pages/ViewSavedGraphsPage';
import FAQPage from './pages/FAQPage';

class App extends Component {
  render() {
    return (  
      <>
       <Router>
       <NavBar/> 
        <div className="container">

          <Switch>          
          <Route path="/" component={MainPage} exact />  
          <Route path="/AboutPage" component={AboutPage} exact />
          <Route path="/DisplayGraphPage" component={graph} exact />
          <Route path="/ViewSavedGraphsPage" component={savedGraphs} exact />
          <Route path="/FAQPage" component={FAQPage} exact />  
          <Route component={NotFound404} />  
          </Switch>
        </div>
       </Router>
       </>
    );
  }
}


export default App;
