import React from 'react';
import './../App.css'
import example1 from './../images/GraphEasyImg4.png'

const FAQPage = () => {
    return (
        <>
            <div className="FAQPage">
                <h3>What kind of files can I upload?</h3>
                <p>Currently, EasyGraph supports the uploads of Excel files.</p>
                <h3>How should I format my files?</h3>
                <p>You should format your files so that there are two columns: the rightmost column
                will be your X values. These can be numbers or words. Your right column will be your
                    Y values, and should only be numbers.</p><br/>
                <p>Sample input:</p>
                <img src={example1} alt="sample_graph_input" className="exampleImg"/>
                <h3>Why do I need to login to save graphs?</h3>
                <p>In order for us to save your unique graphing data to our DataBase, we need an identifier
                    that you can use to come back and view your graphs.</p>
                <h3>What kind of personal data does EasyGraph store?</h3>
                <p>We only store the User Token provided by Google when you log in. This is a long number
                    that uniquly identifies you. We do not store any names or email address information.</p>
                <h3>How can I export or download my graphs?</h3>
                <p>There is a menu on the far right corner of the graph display. This menu allows you to 
                    download your graphs as a JPEG or PNG file.</p>
            </div>
        </>
    )
}

export default FAQPage;