import React, { useState, useEffect } from 'react';
import ReactStars from "react-rating-stars-component";
import DisplayComments from "./DisplayComments"
import "./Comments.css"


const Comments = () => {
    const [commentText, setCommentText] = useState('');
    const [commentName, setCommentName] = useState('');
    const [starRating, setStarRating] = useState(5);
    const [commentsObject, setCommentsObject] = useState({ name: "commentsObject", data: []});
    const [warningLabel, setWarningLabel] = useState("Please log in first to leave a comment!");

    useEffect(() => {  
        const fetchData = async () => {
            const result = await fetch(`/api/getComments`)
            const body = await result.json();            
            setCommentsObject(body);           
        }
        fetchData();

        if (window.user !== null) {            
            setWarningLabel("");
        }
    }, []);

    const addComment = async () => {

        if (window.userToken !== null) {
            if (commentName === "" && commentText === "")
                setWarningLabel("Please fill out the name and comments sections!")
            else if (commentName === "")
                setWarningLabel("Please fill out the name section!")
            else if (commentText === "")
                setWarningLabel("Please fill out the comments section!")
            else { 
                const newComment = { name: commentName, text: commentText, rating: starRating };
                const p = await fetch(`/api/saveComment`, {
                    method: 'post',
                    body: JSON.stringify({ newComment: newComment }),
                    headers: { 'Content-Type': 'application/json', }
                });
                const body = await p.json();
                setCommentsObject(body);
                setCommentName("");
                setCommentText("");
                setStarRating(5);
            }
        } else {
            setWarningLabel("Please log in first to leave a comment!");
        }
    }    

    const ratingChanged = (newRating) => {
        setStarRating(newRating);
        console.log(starRating);
    };

    return (
        <>

            <div class="div3" > 
                <div class="div2">
                <label> Give us a rating!
                <ReactStars
                    count={5}
                    size={24}
                    value={5}
                    edit={true}
                    activeColor="#ffd700"
                    onChange={ratingChanged}
                />
                
                </label> </div> <br/>
                <label>Name:
                    <input type="text" value={commentName} onChange={(event) => setCommentName(event.target.value)} />
                </label>
                <label>Comment:
                    <textarea rows="4" cols="131" value={commentText} onChange={(event) => setCommentText(event.target.value)} />
                </label>
                <button className="submitComments" onClick={() => addComment()}>Add comment</button>
                <p id="warningLabel">{warningLabel}</p>
            </div>

            <br/>
            <DisplayComments commentsObject={commentsObject} />
        </>           
    )
}

export default Comments;