import React, { Component } from 'react';
import { GoogleLogin, GoogleLogout } from 'react-google-login';
import { refreshTokenSetup } from './RefreshToken';

const clientId = '88639351004-p7m3uac1cipk758bqk08p63aqfk43mad.apps.googleusercontent.com';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loggedIn: false
        }
    }

    render() {
        const onLoginSuccess = (res) => {
            this.setState({ loggedIn: true });
            window.userToken = res.profileObj.googleId;
            console.log('Login Success: currentUser:', res.profileObj);
            console.log('User Token', window.userToken)

            //alert(`Logged in successfully welcome ${res.profileObj.name} . \n See console for full profile object.`);

            refreshTokenSetup(res);
        };

        const onLoginFailure = (res) => {
            console.log('Login failed: res:', res);
            alert(
                `Failed to login.`
            );
        };

        const onLogoutSuccess = () => {
            this.setState({ loggedIn: false });
            console.log('Logout made successfully');
            window.userToken = null;
            //alert('Logout made successfully ');
        };

        return (
            <div>
                <div style={this.state.loggedIn ? { pointerEvents: "none", opacity: "0", height: "0px" } : {}}>
                    <GoogleLogin
                        clientId={clientId}
                        buttonText="Login"
                        onSuccess={onLoginSuccess}
                        onFailure={onLoginFailure}
                        cookiePolicy={'single_host_origin'}
                        style={{ marginTop: '100px' }}
                        isSignedIn={true}
                    />
                </div>
                <div style={!this.state.loggedIn ? { pointerEvents: "none", opacity: "0", height: "0px" } : {}}>
                    <GoogleLogout
                        clientId={clientId}
                        buttonText="Logout"
                        onLogoutSuccess={onLogoutSuccess}
                    ></GoogleLogout>
                </div>
            </div>
        );
    }
}
export default Login;

