import React, { useState, useEffect } from 'react';
import Table from '../Table';
import '../Graph.css'
import CanvasJSReact from './../canvasLib/canvasjs.react';

var CanvasJSChart = CanvasJSReact.CanvasJSChart;

function Graph() {

    const [graphData, setGraphData] = useState([]);
    const [label, setLabel] = useState();
    const [displayContents, setDisplayContent] = useState();
    const [buttonStyle, setButtonStyle] = useState({ color: "red" })

    useEffect(() => {

        if (window.userToken !== null) {
            setLabel("Here are your graphs!");
            setButtonStyle({ color: "black" });
            fetchData();
            setDisplayContent("")
            console.log("retrieved graph data");
        } else {
            setLabel("Log in to display your graphs!");
            setDisplayContent("none")
        }
    }, []);

    const fetchData = async () => {
        const savedGraphData = await fetch(`/api/viewSavedGraphs`, {
            method: 'post',
            body: JSON.stringify({ userToken: window.userToken }),
            headers: { 'Content-Type': 'application/json', }
        });
        const savedGraphObject = await savedGraphData.json();
        if (savedGraphData !== null)
            setGraphData(savedGraphObject);
        else
            setLabel("You do not have any graphs saved in the DB");
        //console.log("inside fetchDATa");
    }

    const deleteFromDB = async (index) => {
        const d = await fetch(`/api/deleteSavedGraph`, {
            method: 'post',
            body: JSON.stringify({ userToken: window.userToken, title: index }),
            headers: { 'Content-Type': 'application/json', }
        });
        console.log(index);
        fetchData();
    }
       
    return (        
        <>
            <h1 style={buttonStyle}>{label}</h1>
            <div className="savedGraphPageDisplay">
                <table style={{width: "100%", display: displayContents}}>
                {graphData ? graphData.reverse().map((item, key) => {
                    return <tr key={key} >
                        <td className="savedGraphTableData">
                        <Table data={item.data[0].dataPoints} className="savedGraphTable" />
                            <button className="deleteFromDB" type="button" onClick={() => deleteFromDB(item.title.text)}>Delete From DataBase</button>
                            <div className="graphDisplay">
                                <CanvasJSChart options={item} /></div>
                        </td>
                        </tr>;
                    })
                        : ""}
                    </table>                                
            </div>            
        </>
    );
}

export default Graph;