import React, { Component } from 'react';
import './Graph.css'
import CanvasJSReact from './canvasLib/canvasjs.react';
import Select from 'react-select';
import { read, utils } from 'xlsx';
import Table from './Table';

var CanvasJS = CanvasJSReact.CanvasJS;
var CanvasJSChart = CanvasJSReact.CanvasJSChart;

var inputObjects = [
    
    { label: "0", y: 10 },
    //{ label: "Banana", y: 25 },
    //{ label: "Mango", y: 30 },
    //{ label: "Grape", y: 28 }
]

const SheetJSFT = [
    "xlsx", "xlsb", "xlsm", "xls", "xml", "csv", "txt"
].map(function (x) {
    return "." + x;
}).join(",");

class SplineGraph extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: "Title",
            axisY: "Y-Axis Label",
            axisX: "X-Axis Label",
            graphType: "spline",
            warningLabel: "",
            graphSaved: false,
            data: [],
            cols: [],
            input: inputObjects
        }
    }


    render() {
        const options = {
            animationEnabled: true,
            zoomEnabled: true,
            exportEnabled: true,
            title: {
                text: this.state.title
            },

            axisY: {
                title: this.state.axisY
            },

            axisX: {
                title: this.state.axisX
            },

            data: [{
                type: this.state.graphType,
                legendText: this.state.xaxis,
                dataPoints: this.state.input
            }]
        }

        const selectOptions = [
            {
                value: 1,
                label: "Line Chart"
            },
            {
                value: 2,
                label: "Pie Chart"
            },
            {
                value: 3,
                label: "Column Chart"
            },
            {
                value: 4,
                label: "Area Chart"
            },
            {
                value: 5,
                label: "Bar Chart"
            },
            {
                value: 6,
                label: "Scatter Chart"
            }
        ];

        const handleChange = obj => {

            switch (obj.value) {
                case 1:
                    this.setState({ graphType: 'spline' })
                    break;
                case 2:
                    this.setState({ graphType: 'pie' })
                    break;
                case 3:
                    this.setState({ graphType: 'column' })
                    break;
                case 4:
                    this.setState({ graphType: 'area' })
                    break;
                case 5:
                    this.setState({ graphType: 'bar' })
                    break;
                case 6:
                    this.setState({ graphType: 'scatter' })
                    break;
            }
        }

        const titleUpdate = obj => {            
            this.setState({ title: document.getElementById("titleInput").value })
        }

        const yaxisUpdate = obj => {
            this.setState({ axisY: document.getElementById("yaxisInput").value })
        }

        const xaxisUpdate = obj => {
            this.setState({ axisX: document.getElementById("xaxisInput").value })
        }

        const saveToDB = async () => {
            console.log("button clicked");
            if (window.userToken !== null) {
                //console.log("Logged in");

                const validTitle = await fetch(`/api/validateGraph`, { 
                    method: 'post',
                    body: JSON.stringify({ userToken: window.userToken, title: this.state.title }),
                    headers: {
                        'Content-Type': 'application/json',                        
                    }
                });                
                const isValid = await validTitle.json();
                
                if (isValid) {
                    //console.log("Title is valid!")
                    this.setState({ graphSaved: true,  warningLabel: "Graph saved!" });

                    const obj = {
                        userToken: window.userToken,
                        graphOptions: options
                    }
                    
                    console.log(obj);
                    //console.log("saving to DB");
                    const savedGraphObject = await fetch(`/api/saveGraph`, {
                        method: 'post',
                        body: JSON.stringify(obj),
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    });
                    //console.log("finished saving to DB");
                              
                } else {
                    //console.log("Title is not valid");
                    this.setState({ warningLabel: "Please use a unique title to save your graph." });
                }
            } else {
                //console.log("Not logged in");
                this.setState({ warningLabel: "Please log in before saving a graph." });
            }
        }

        const make_cols = (refstr: any) => {
            let o = [], C = utils.decode_range(refstr).e.c + 1;
            for (var i = 0; i < C; ++i) o[i] = [utils.encode_col(i)]
            return o;
        };

        const onLoadFile = (e: { target: { files: any; }; }) => {
            console.log("Calling Sky Captain");
            const files = e.target.files;
            if (files && files[0]) handleFile(files[0]);
        }

        const handleFile = (file: Blob/*:File*/) => {
            /* Boilerplate to set up FileReader */
            const reader = new FileReader();
            const rABS = !!reader.readAsBinaryString;
            reader.onload = (e) => {
                /* Parse data */
                const bstr = e.target.result;
                const wb = read(bstr, { type: rABS ? 'binary' : 'array' });
                /* Get first worksheet */
                const wsname = wb.SheetNames[0];
                const ws = wb.Sheets[wsname];
                /* Convert array of arrays */
                const data = utils.sheet_to_json(ws, { header: 1 });
                /* Update state */
                this.setState({ data: data, cols: make_cols(ws['!ref']) });

                createArray();
            };
            if (rABS) reader.readAsBinaryString(file); else reader.readAsArrayBuffer(file);
        };

        const createArray = () => {

            console.log("inside create Array");
            let temp = [];

            for (var i = 0; i < this.state.data.length; i++) {
                temp.push({ label: this.state.data[i][0], y: this.state.data[i][1] })
            }            
            this.setState({ input: temp });          
            console.log(this.state.input);
            this.setState({ graphSaved: false, warningLabel: "" });
        }


        return (
            <div className="pageDisplay">
                < Table data={this.state.input} />
                <div>
                    <form className="graphMenu">
                        <input type="file" onChange={onLoadFile} accept={SheetJSFT} /><br/>
                        <label >Title:</label>
                        <input type="text" id="titleInput" name="titleInput" onChange={titleUpdate} /><br/>
                        <label >Y-Axis:</label>
                        <input type="text" id="yaxisInput" name="yaxisInput" onChange={yaxisUpdate} /><br/>
                        <label >X-Axis:</label>
                        <input type="text" id="xaxisInput" name="xaxisInput" onChange={xaxisUpdate} /><br/><br/>
                        <Select
                            value={this.state.graphtype}
                            options={selectOptions}
                            onChange={handleChange}
                        /><br/>
                        <button disabled={this.state.graphSaved} className="saveToDBBtn" type="button" onClick={saveToDB}>Save to DataBase</button>
                        <p id="warningLabel">{this.state.warningLabel}</p>
                    </form>                
                    <div className="graphDisplay"><CanvasJSChart options={options}/></div>                                               
                    </div>
                </div>
        );
    }
}

export default SplineGraph